
@import "../module_scss/font_customize";

$font_color: #333;
$btnSide_color: rgba(255, 255, 255, 0.219);
$logo_color: #ff7b1a;
$logo_color_hover: #ff791ab7;

$main_item_imgW: 240px;
$main_item_imgH: 153.4px;


*{
    font-family: "Microsoft YaHei";
    scroll-behavior: smooth;
}
body{
    background-color: #f8f8f8;
}

//Header
.header{
    z-index: 1;
    display: block;
    position: fixed;
    top: 0;
    width: 100%;
    height: 105px;
    background-color: rgba(255, 255, 255, 0.315);
    

    .logo{
        display: inline-block;
        background-image: url(../images/TTTTaiwan-V2.png);
        background-size: 100%;
        background-repeat: no-repeat;
        width: 95px;
        height: 95px;
        position: absolute;
        top: 5px;
        left: 26%;


    }
    .nav_item_container{
        position: sticky;
        width: 26%;
        margin-top: 25px;
        margin-left: 59%;
        display: flex;
        justify-content: space-between;

        .nav_item{
            display: inline-block;
            text-align: center;
            margin-right: 5%;
            margin-left: 5%;

            .a:link,:visited{
                @include font(3);

                text-decoration-line: none;
                color: $font_color;
            }
            .a{
                display: block;
                width: 97px;
                height: 80px;
            }
            .a:hover{
                color: #000;
            }

        }
    }
    .hover_item_container{
        width: 100%;
        position: sticky;
        display: flex;
        justify-content: space-between;

        .hover_nav{
            width: 40%;
            margin-left: 53%;
            height: 140px;
            background-color: #d8d8d8c4;
            text-align: center;
            border-radius: 20px;

            .hover_item{
                @include font(1);
                color: #623A28;
                text-align: center;
                font-weight: 850;
                line-height: 50px;
                cursor: pointer;
                display: inline-block;
                margin-right: 0.45vw;
                margin-left: 0.45vw;
                margin-top: 12px;
                width: 16%;
                height: 120px;
                background-size: 45%;
                background-position: 50% 70% ;
                background-repeat: no-repeat;
            }
            .hover_item:hover{
                color: #a7522b;
            }
            .line{
                display: inline-block;
                width: 2px;
                height: 80px;
                background-color: #ff791a8c;
                margin-left: 1px;
                color: #00000000;
            }
        }
    }
}

//Banner
.banner_container{
    width: 100%;
    position: relative;
    z-index: 0;
    overflow: hidden;

    .banner_repeater{
        display: flex;
        width: 100%;
        margin-left: 0%;
        transition: all 0.7s;
        
        .img_container{
            
            width: 100%;
            flex-shrink: 0;
            
            img{
                width: 100%;
                height: 100%;
                
            }
        }
    }
    .handle_carousel{
        display: flex;
        position: absolute;
        bottom: 2%;
        right: 50%;
        transform: translate(50%, 0);

        .handle_btn{
            width: 12px;
            height: 12px;
            border: solid 2px rgba(255, 255, 255, 0.219);
            border-radius: 50%;
            margin-right: 10px;
            margin-left: 10px;
            background-color: rgba(255, 255, 255, 0.055);
            cursor: pointer;
            transition: all 0.7s;

        }
    }
}

//ActivityMain FoodMain SceneryMain
.main_container{
    width: 100%;
    height: 808px;
    max-width: 1980px;
    margin-left: auto;
    margin-right: auto;

    .first{
        position: relative;
        width: 100%;

        .main_title{
            width: 54%;
            margin-top: 100px;
            margin-bottom: 30px;
            margin-right: auto;
            margin-left: auto;
            
            @include font(4);
            
            font-weight: 550;
        }
        .main_title::before{
            content: ".";
            color: #00000000;
            
            background-color: $logo_color;
        }
        .main_items_container{
            margin-left: auto;
            margin-right: auto;
            width: 57%;

            display: flex;
            justify-content: start;
            flex-wrap: wrap;
            
        }
        .btn_container{
            width: 50%;
            height: 30px;
            margin: 20px auto 80px auto;

            display: flex;
            justify-content: center;

            .left_btn{
                @extend .btn;

            }
            .right_btn{
                @extend .left_btn;
            }
        }
    }
}
//MainItems
.item_a{
    flex-basis: 240px;
    height: 270px;
    margin-left: 14px;
    margin-right: 14px;
    margin-bottom: 45px;
    text-align: center;
    

    .item_container{
        background-color: white;
        border-radius: 10px;

        i{
            display: inline;
        }
        img{
            width: $main_item_imgW;
            height: $main_item_imgH;
            border-radius: 10px 10px 0px 0px;
        }
        .title_container{
            
            width: 200px;
            height: 84px;
            margin-left: auto;
            margin-right: auto;

            overflow: hidden;
            

            .item_title{
                
                @include font(2);
                font-weight: 600;
                
                
            }
        }
        .caption_container{
            margin-top: 10px;

            i{
                color: $logo_color;
            }
            .item_caption{
                display: inline-block;
                margin-left: 5px;
                color: $font_color;
                font-weight: 600;
            }
        }
    }
    .item_container:hover{
        box-shadow: 1px 1px 7px 1px #c7c7c7dc;
    }
}
.item_a:visited, :link{
    color: $font_color;
    text-decoration: none;
}

//MainPageBtn    ****** 收進SCSS_ALL ******
.btn{
    font-weight: 500;

    text-align: center;
    width: 25px;
    height: 25px;

    border-radius: 50%;
    margin-left: 3px;
    margin-right: 3px;
    cursor: pointer;

    
    
}
.btn:hover{
    color: white;
    background-color: $logo_color;
}

//ScrollTop      ****** 收進SCSS_ALL ******
.to_top_btn_container{
    position: fixed;
    right: 15px;
    bottom: 10px;
    width: 45px;
    height: 45px;
    
    border-radius: 10px;
    background-color: $logo_color;

    cursor: pointer;

    .to_top_btn{
        width: 19px;
        height: 34px;
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, -50%);

        font-size: 25px;
        color: white;
        background-color: $logo_color;
    }
}
.to_top_btn_container:hover{
    opacity: 0.85;
}

//SearchBtn
.input_container{
    display: flex;
    justify-content: center;
    align-items: center;
    
    position: absolute;
    top: 444px;
    right: 50%;
    transform: translate(50%,0);
    background-color: white;
    border-radius: 7px;
    width: 28vw;
    height: 45px;
    
    
    i{
        @include font(2);

    }
    input{
        @include font(1);

        width: 18vw;
        height: 38px;
        margin-left: 10px;
        color: $font_color;
        background-color: white;
        border: solid 1px white;
    }
    input:focus{
        border: solid 2px $logo_color;
        border-radius: 5px;
        outline: solid white 1px
    }

    .GO_a{

        .GO_btn{
            @include font(1,29px);
            
            font-weight: 600;
            text-align: center;
            
            cursor: pointer;
            
            width: 94px;
            height: 30px;
            background-color: $logo_color;
            border-radius: 20px;
            margin-left: 20px;
        }
        .GO_btn:hover{
            background-color: $logo_color_hover;
            
        }
    }
    .GO_a :link, :visited{
        color: $font_color;
    }
}

//Search
.search_main{
    width: 100%;
    margin-top: 70px;

    .tips_container{
        width: 57%;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }    
    .result_container{
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
}

//SearchTips
.tips{
    height: 55px;
    background-color: rgb(192, 192, 192);
    border-radius: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    display: inline-block;
    
    .container{
        text-align: center;
        
        i{
            display: inline-block;
            margin-left: 15px;
            margin-right: 5px;
        }
        p{
            display: inline-block;
            margin-right: 20px;
        }
    }
}

//Footer
footer{
    width: 100%;
    height: 200px;
    background-color: #d8d8d8;
    text-align: center;
    position: relative;

    ul{
        list-style-type: none;
        position: absolute;
        top: 50px;
        left: 50%;
        transform: translate(-50%, 0);

        li{
            margin-bottom: 20px;
            @include font(1);
            color: $font_color;
        }
    }
}

//PageBanner
.page_banner_container{
    width: 100%;
    

    .page_banner{
        color: transparent;
        width: 100%;
        height: 180px;
        background-image: url();
        background-size: 100%;
        background-repeat: no-repeat;
    }
}

//ItemsPageMain
.main_page_container{
    width: 100%;
    height: 600px;
    position: relative;
    background-color: white;

    .page_img_container{
        width: 40%;
        display: inline-block;
        position: absolute;
        top: 100px;
        left: 10%;
        
        .page_img_big{
            width: 63%;
            margin-left: auto;
            margin-right: auto;

            img{
                width: 100%;
                height: 320px;
            }
        }
        .page_img_small{
            display: flex;
            justify-content: space-between;
            width: 63%;
            margin-left: auto;
            margin-right: auto;

            img{
                width: 32%;
                height: 102.5px;
                cursor: pointer;
            }
        }
    }
    .text_container{
        width: 40%;
        display: inline-block;
        position: absolute;
        top: 79px;
        right: 10%;
        
        .page_title{
            @include font(2);
            font-weight: 600;
            color: $font_color;
            margin-top: 20px;
            margin-bottom: 27px;
        }
        .page_description{
            @include font(1);
            color:$font_color;
            margin-top: 8px;
            margin-bottom: 42px;
        }
        .open_time{
            @extend .page_description;
            width: 60%;
            

            i{
                color: $logo_color;
            }
            span{
                margin-left: 4px;
            }
        }
        .position{
            display: flex;
            

            .address{
                @extend .page_description;
                margin-right: 100px;

                i{
                    color: $logo_color;
                }
            }
            .google_map{
                @extend .page_description;
                font-weight: 600;
                
                i{
                    color: $logo_color;
                }
                a:visited{
                    color: $font_color;
                    
                    i{
                        color: $logo_color;
                    }
                }
                a:hover{
                    color: black;
                }
            }
        }
        .website{
            @extend .page_description;

            a{

                i{
                    color: $logo_color;
                }
            }
            a:visited{
                color: $font_color;
                
                i{
                    color: $logo_color;
                }
            }
            a:hover{
                color: black;
            }
        }
        .phone{
            @extend .page_description;

            i{
                color: $logo_color;
            }
        }
    }
}

//ItemsPageCarousel
.page_carousel_container{
    width: 100%;
    height: 530px;

    .carousel_title{
        text-align: center;
        @include font(2);
        font-weight: 800;
        margin-top: 75px;

        i{
            color: $logo_color;
        }
    }
    .carousel_repeater{
        
        width: 65%;
        height: 290px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 55px;
        overflow: hidden;

        .item_container{
            
            width: 65%;
            height: 290px;
            margin-left: 5px;
            display: flex;
            flex-wrap: nowrap;
            transition: all 1.3s;
            
        //CarouselItems
            .item{
                width: 230px;
                height: 265px;
                margin-top: 5px;
                background-image: url();
                background-repeat: no-repeat;
                background-size: 100% 100%;
                border-radius: 10px;
                margin-left: 8px;
                margin-right: 7px;
                display: flex;
                flex-direction: column;
                justify-content: end;
                transition: all 1s;

                .item_name{
                    text-align: center;
                    font-size: 16px;
                    font-weight: 600;
                    color: white;
                    margin-bottom: 13px;
                }
                .item_open_time{
                    text-align: center;
                    color: white;
                    font-size: 8px;
                    margin-bottom: 15px;
                }
            }
            .item:hover{
                background-size: 110% 110%;
            }
        }
    }
}



